      this.tempProduct = this.memberData; // 
      this.getBankCard();
      this.getWalletInfo();
    },
  },
  data() {
    //   
    const checkPwd = (rule, value, callback) => {
      const reg = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,12}$/;
      if (!reg.test(value)) {
        callback(new Error('812 '));
      } else {
        callback();
      }
    };
    //  -
    const checkPwd3 = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(''));
      } else if (value !== this.rwdValue.pwd) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      orderType: 'ASC', // ID(DESC , ASC )
      orderBy: '', // ( ... money )
      modal: {},
      tempProduct: {},
      // /
      online_Status: {
        member_id: '',
      },
