<script>
import Modal from 'bootstrap/js/dist/modal';

export default {
  data() {
    //   
    const checkPwd2 = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error(''));
      } else if (value !== this.ruleForm.pwd) {
        callback(new Error(''));
      } else {
        callback();
      }
    };

    return {
      modal: {},
      ruleForm: {
        name: '',
        pwd: '',
        checkPass: '',
        currency: '',
        delivery: false,
        type: [],
        phone: '',
        country: '',
        birthday: '',
        vip_group: '',
        email: '',
        city: '',
        Im1: '',
        Im2: '',
      },
      rules: {
        name: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
          {
            min: 1,
            max: 10,
            message: '1',
            trigger: 'blur',
          },
        ],
        account: [
          {
            required: true,
            message: ' 87',
            trigger: 'blur',
          },
        ],
        pwd: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
