      this.$refs.ruleFormRef1.resetFields();
      this.$refs.ruleFormRef.resetFields();
    },
    // 
    doRegister() {
      const testapi = `${process.env.VUE_APP_TESTAPI}`;
      // 
      this.$refs.ruleFormRef1.validate((valid) => {
        // 
        if (!valid) {
          console.log(',');
          return false;
        }
        // truefalse
        console.log('');
        // 
        // 
        // ....
        // axios
        this.$http
          .post(`${testapi}/backend/members/members_add`, this.ruleForm)
          .then((response) => {
            if (response.data.code === 200) {
              this.$swal.fire('Welcome!', '!', 'success');
              this.modal.hide();
              this.$router.push('/memberlist');

              console.log(response.data);
            } else {
              this.$swal.fire('', `${response.data.msg}`, 'error');
              console.log(response.data);
            }
          })
          .catch((error) => {
            console.log(error);
            console.log('');
          });

        return true;
      });
      //   resetForm(); // 
    },
  },
